// ABOUt//
.about {
  background-color: $section-pink;
  padding-bottom: 39px;
  padding-top: 125px;

  @media screen and (max-width: 767px) {
    background-image: url(../images/about-images/mobile-about-bg.png);
    background-repeat: no-repeat;
    background-position: center bottom 200px;
    background-size: 100% auto;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/about-images/mobile-about-bg2x.png);
      background-repeat: no-repeat;
      background-position: center bottom 200px;
      background-size: 100% auto;
    }
  }
  @media screen and (min-width: 1200px) {
    background-image: url(../images/about-images/desk-about-bg.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100% auto;

    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url(../images/about-images/desk-about-bg2x.png);
      background-repeat: no-repeat;
      background-position: center bottom 50px;
      background-size: 100% auto;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    background-image: none;
  }
}

.left-side {
  @media screen and (min-width: 768px) {
    width: calc(100% / 2 - 15px);
    padding-left: 17px;
  }
}
.about-container {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    // align-items: flex-end;
  }
}
.section-title {
  margin-bottom: 50px;

  @media screen and (min-width: 768px) {
    margin-bottom: 100px;
  }

  margin-bottom: 106px;
}

