.about-button {
  background: $main-white-color;
  border-radius: 22px;
  border: none;
  padding: 19px 38px 17px 36px;
  align-items: center;
  margin: 0;
  font-family: DM Sans;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.28;
  color: $main-pink-color;
  transition: background-color 250ms $timing-function;
  cursor: pointer;

  // @media screen and (min-width: 768px) {
