.hero {
    position: relative;
    background-color: #ffb8ca;
    width: 100%;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    z-index: 0;
    overflow: hidden;
}
.hero-container {
    position: relative;
}
.hero-wrapper {
    width: 100%;
    height: 440px;
}
.slogan {
    position: absolute;
    margin-top: 131px;
    z-index: 5;
    width: 218px;
    height: 106px;
    
}
.slogan-1 {
    font-family: $text-font-family;
    font-weight: 400;
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $main-white-color;
}
.slogan-2 {
    font-family: $text-font-family;
    font-weight: 400;
    font-size: 48px;
    line-height: 0.97;
    text-transform: uppercase;
    color: $main-pink-color;
}
.hero-buttons {
    display: flex;
    margin-top: 20px;
}
    .hero-products {
        width: 61px;
        height: 24px;
        font-weight: 400;
        font-size: 8px;
        line-height: 1.25;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color:  $main-white-color;
        background-color: $main-pink-color;
        border-radius: 20px;
        border: none;
        z-index: 5;
        cursor: pointer;
        transition: background-color 250ms $timing-function;
        text-decoration: none;

    &:hover,
    &:focus {
        background-color: #C7133E;
    }
}
    .made {
        margin-left: 6px;
        width: 83px;
        height: 24px;
        font-weight: 400;
        font-size: 8px;
        line-height: 1.25;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        color: $main-pink-color;
        background-color: $main-white-color;
        border-radius: 20px;
        border: none;
        z-index: 5;
        cursor: pointer;
        transition: background-color 250ms $timing-function;
        text-decoration: none;
        
    &:hover,
    &:focus {
        background-color: $hover-gray;
    }
}
.img-jug {
    display: none;
}
.hero-text {
    display: none;
}
.next {
    display: none;
}
.circle {
    position: absolute;
    display: flex;
    justify-content: center;
    top: 83px;
    left: 36px;
    width: 344px;
    height: 344px;
    background-color: $pink-text-color;
    border-radius: 50%;
    z-index: 0;
}
.hero-img {
    background-image: url("/images/hero/mobile/ice-cream.png");
    position: absolute;
    top: 9px;
    left: 81px;
    width: 171px;
    height: 410px;
}
.hero-right {
    display: none;
}

@media screen and (min-width: 768px) {
    .hero-container {
        display: flex;
    }
    .hero {
        width: 100%;
        height: 100%;
        margin-left: auto;
        margin-right: auto;
    }
    .hero-wrapper {
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 100%;
    }
    .hero-left {
        display: flex;
        flex-direction: column;
    }
    .slogan {
        position: relative;
        display: flex;
        flex-direction: column;
        margin-top: 102px;
        z-index: 5;
        width: 162px;
        height: 80px;
    }
    .slogan-1 {
        font-family: $text-font-family;
        font-weight: 400;
        font-size: 22px;
        line-height: 1.14;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $main-white-color;
    }
    .slogan-2 {
        font-family: $text-font-family;
        font-weight: 400;
        font-size: 34px;
        line-height: 0.94;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $main-pink-color;
    }
    .hero-left-milk {
        width: 245px;
        margin-top: 59px;
    }
    .hero-left-milk-wrap {
        display: flex;
        justify-content: space-between;
    }
    .img-jug {
        display: flex;
        background-image: url("/images/hero/tablet/jug.png");
        background-repeat: no-repeat;
        left: 24px;
        top: 28px;
        width: 124px;
        height: 126px;
    }
    .hero-text-wrap {
        display: flex;
        flex-direction: column;
        padding-top: 47px;
    }
    .hero-text {
        display: block;
        width: 121px;
        height: 48px;
        font-weight: 400;
        font-size: 8px;
        line-height: 1.5;
        color: $main-white-color;
        text-align: left;
    }
    .next {
        display: block;
        padding-left: 4px;
        width: 18px;
        height: 18px;
        margin-top: 7px;
        border-radius: 50%;
        background-color: $main-white-color;
        cursor: pointer;
    }
    .next:hover,
    .next:focus {
        background-color: $mobile-menu-bg-color;
        transform: scale(1.05) rotate(360deg);
    }
    .next-arrow {
        fill: #C7133E;
        width: 10px;
        height: 12px;
    }
    .circle {
        position: absolute;
        top: 31px;
        left: 317px;
        width: 324px;
        height: 324px;
        background-color: $pink-text-color;
        border-radius: 50%;
    }
    .hero-img {
        background-image: url("/images/hero/tablet/ice-cream.png");
        background-repeat: no-repeat;
        position: relative;
        top: 31px;
        left: -23px;
        width: 143px;
        height: 336px;
    }
    .hero-right {
        display: flex;
        flex-direction: column;
        align-items: end;
        z-index: 0;
        margin-top: 96px;
    }
    .hero-right-numbers {
        display: flex;
        flex-direction: column;
        width: 50px;
        height: 44px;
        
    }
    .hero-right-img {
        margin-top: 128px;
        width: 168px;
        height: 133px;
        background-image: url("/images/hero/tablet/tasty.png");
        background-repeat: no-repeat;
    }
    .number {
        font-family: $text-font-family;
        font-weight: 400;
        font-size: 28px;
        line-height: 1.14;
        text-align: right;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $main-pink-color;
    }
    .number.number::before {
        content: '';
        display: block;
        margin-left: 43px;
        margin-bottom: -14px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: $main-white-color;
    }
    .number-text {
        font-weight: normal;
        font-size: 8px;
        line-height: 10px;
        text-align: right;
        color: $main-white-color;
    }

}

@media screen and (min-width: 1200px) {
    .hero-container {
        display: flex;
    }
    .hero-wrapper {
        justify-content: space-between;
        width: 100%;
        height: 100%;
    }
    .slogan {
        margin-top: 171px;
        width: 277px;
        height: 137px;
    }
    .slogan-1 {
        font-size: 38px;
        line-height: 1.16;
    }
    .slogan-2 {
        font-size: 58px;
        line-height: 0.95;
    }
    .hero-buttons {
        margin-top: 25px;
    }
    .hero-products {
        width: 104px;
        height: 40px;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.29;
    }
    .made {
        margin-left: 17px;
        width: 138px;
        height: 40px;
        font-size: 14px;
        line-height: 1.29;
}
    .hero-left-milk {
        width: 418px;
        margin-top: 111px;
        margin-left: -34px;

    }
    .circle {
        position: absolute;
        top: 50px;
        left: 400px;
        width: 538px;
        height: 538px;
    }
    .hero-img {
        background-image: url("/images/hero/desctop/ice-cream.png");
        background-repeat: no-repeat;
        position: relative;
        top: 61px;
        left: -23px;
        width: 236px;
        height: 557px;
    }
    .img-jug {
        display: flex;
        width: 188px;
        height: 191px;
        background-image: url("/images/hero/desctop/jug.png");
        background-repeat: no-repeat;
    }
    .hero-text {
        width: 200px;
        height: 78px;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.4;
        color: $main-white-color;
        text-align: left;
    }
    .next {
        display: flex;
        justify-content: center;
        text-align: center;
        width: 26px;
        height: 26px;
        margin-top: 20px;
        padding-top: 8px;
        padding-left: 1px;
    }
    .next-arrow {
        width: 12px;
        height: 20px;
    }
    .hero-right-numbers {
        width: 50px;
        height: 44px;
        margin-right: 32px;
    }
    .hero-right {
        margin-top: 156px;
    }
    .hero-right-img {
        margin-top: 236px;
        width: 293px;
        height: 232px;
        background-image: url("/images/hero/desctop/tasty.png");
        background-repeat: no-repeat;
    }
    .number {
        font-family: $text-font-family;
        font-weight: 400;
        font-size: 46px;
        line-height: 1.15;
    }
    .number.number::before {
        content: '';
        margin-left: 67px;
        margin-bottom: -23px;
        width: 30px;
        height: 30px;
    }
    .hero-numbers-cafes {
        text-align: right;
        width: 81px;
    }
    .hero-numbers-trucks {
        text-align: right;

        width: 81px;
    }
    .number-text {
        display: flex-end;
        flex-wrap: nowrap;
        align-content: flex-start;
        font-size: 14px;
        line-height: 1.29;
    }
    .hero-number-items {
        flex-wrap: nowrap;
    }
    .mobile-menu {
        display: none;
    }
}